@import url('https://fonts.googleapis.com/css?family=Josefin+Sans');
  /* ========================== MAIN NAVIGATION ============================ */


 h2, h3, h4, h5{
  font-family:'Josefin Sans';
}

.menu {
  float: right;
  clear: left;
  margin: 1em;
  font-size: 1.1rem;
  color: white; 
}
  @media (min-width: 867px) {
    .menu {
      display: none; 
    } 
  }

.main-heading {
  margin: 1.1em 0 0 1em;
  float: left; 
}
  .main-heading h1 {
    color:rgb(3,151,214);
    margin: 0; 
    text-transform: uppercase;
    font-weight:normal;
    font-size:1.6em;
    
}
h1 span{
  color:white;
  font-family:Impact;
  letter-spacing: 1px;
  font-size:24px;
}

  @media (min-width: 867px) {
    .main-heading {
      margin: auto; 
    }
  }

.wrapper {
  z-index: 3;
  width: 100%;
  position: fixed;
  background:
  -webkit-gradient(
    linear, left top, left bottom, from(rgba(56,54, 58, 1)), to(rgba(35, 33, 39, 1))
  );
  background:
  -webkit-linear-gradient(
    rgba(56,54, 58, 1), rgba(35, 33, 39, 1)
  );
  background:
  -o-linear-gradient(
    rgba(56,54, 58, 1), rgba(35, 33, 39, 1)
  );
  background:
  linear-gradient(
    rgba(56,54, 58, 1), rgba(35, 33, 39, 1)
  );
  height: 60px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center; 
  -webkit-box-shadow: 0 1px 6px 0 rgba(64,196,253,1); 
          box-shadow: 0 1px 6px 0 rgba(64,196,253,1);
}

.navigation {
  z-index: 3;
  width: 100%;
  position: fixed; 
}

  @media (min-width: 867px) {
    .navigation {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: row;
          flex-direction: row;
      -ms-flex-pack: distribute;
          justify-content: space-around;
      -ms-flex-align: center;
          align-items: center;
      
    } 
    }



.topnav {
  text-align: center;
  list-style-type: none;
  padding: 0;
  margin: 0;
  width: 100%;
  overflow-y: hidden;
  height: 0;
  z-index: 10;
  background-color: rgb(30,30,30);
}

  .topnav li {
    border-top: 1px solid #E6E6E6;
    padding: 1em; 
    
  }

  @media (min-width: 867px) {
    .topnav {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: row;
          flex-direction: row;
      -ms-flex-pack: end;
          justify-content: flex-end;
      -ms-flex-align: center;
          align-items: center;
      height: 60px;
      -webkit-transition: 0s;
      -o-transition: 0s;
      transition: 0s;
      background-color: transparent;
      -webkit-box-shadow: none;
              box-shadow: none; 
      
    }
  .topnav li {
        border-top: none;
        border-right:1px solid rgb(65,63,68);
        border-left:1px solid rgb(40,38,42);
        padding: 1.2em 0.5em; 
      } 
  }

li a {
  color: white;
  font-family: "Josefin Sans", sans-serif;
  font-size: 12px; 
  text-decoration:none;
  text-transform: uppercase;

}
  @media (min-width: 867px) {
    li a {
      margin: 1em;
    } 
  }

.current {
  color: #C49B66; 
}

.tag:hover {
  text-decoration: none;
  color: #C49B66;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
  opacity: 0.5; 
}

.top {
  width: 100%;
  height: 20px;
  -webkit-transform: translateY(70px);
      -ms-transform: translateY(70px);
          transform: translateY(70px); 
}
/* ========================= RESPONSIVE CLASSES ============================ */
.response {
  height: 170px;
  -webkit-transition: 0.4s ease-in-out;
  -o-transition: 0.4s ease-in-out;
  transition: 0.4s ease-in-out; 
}
  @media (min-width: 867px) {
    .response {
      height: 70px;
      -webkit-transition: none;
      -o-transition: none;
      transition: none; 
    } 
  }

  .active{
    background-color:rgb(52,52,52);
  }
/* ============================ SUBHEADER SECTION ============================== */

.subheader{
  background:  -webkit-radial-gradient(
    circle,
  rgb(46,45,51),
    rgb(6,5,11)
  );
  background:  -o-radial-gradient(
    circle,
  rgb(46,45,51),
    rgb(6,5,11)
  );
  background:  radial-gradient(
    circle,
  rgb(46,45,51),
    rgb(6,5,11)
  );

  height:100px;
  background-size: cover;
  padding-top:60px;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align:center;
      align-items:center;
}

  /* ========================== MOVIE CONTAINER ============================ */

  .movie-container{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    padding:30px 10px;
    background-color:rgba(36,35,41);
   
  }
  .movie-item{
    width:50vh;
    height:70vh;
    padding:2px 3px;
    margin:5px;
   background-color:rgb(43,42,45);
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    -ms-flex-direction:column;
        flex-direction:column;
    -webkit-box-shadow: 0 0 4px rgba(0,0,0,0.6), 0 3px 6px rgba(0,0,0,0.6);
            box-shadow: 0 0 4px rgba(0,0,0,0.6), 0 3px 6px rgba(0,0,0,0.6);
    cursor:pointer;
    -webkit-transition:0.3s;
    -o-transition:0.3s;
    transition:0.3s;
  }
  .movie-item:hover{
    -webkit-box-shadow: 0 0 7px white, 0 0 7px white;
            box-shadow: 0 0 7px white, 0 0 7px white;
   
  }
  .text-container{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-direction:row;
        flex-direction:row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    background-color:black;
    width:100%;
    padding:10px 0;
    text-transform: uppercase;
    -ms-flex-pack: center;
        justify-content: center;
    border:1px solid black;
  }
.text-container h6{
  color:white;
  margin:0;
  white-space: nowrap;
  overflow: hidden;
  padding:0 5px;
  font-weight:normal;
}

  .wrap{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    -ms-flex-direction:column;
        flex-direction:column;
  }
  
  .movie-cover{
    z-index: 1;
    background-repeat: no-repeat;
    background-size: cover;
    border:1px solid black;
    position: relative;
  }
  @media(min-width:568px){
    .wrap{
      -ms-flex-direction:row;
          flex-direction:row;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
    }
    .movie-item{
      width:195px;
      height:295px;
    }
  }
  @media(min-width:2140px){
    .movie-container{
      width:2140px;
    }
  }
  /* ========== INPUT ===============*/

  input{
    background-color:rgb(25,25,25);
    border:none;
    height:30px;
    width:300px;
    border-radius:3px;
    color:white;
  }
 ::-webkit-input-placeholder{
    padding-left:10px;
  }
 :-ms-input-placeholder{
    padding-left:10px;
  }
 ::-ms-input-placeholder{
    padding-left:10px;
  }
 ::placeholder{
    padding-left:10px;
  }
  /* ===================== MOVIE RATING ================= */
  .movie-rating {
    position: absolute;
    right: 0px;
    top:0px;
    width: 40px;
    height: 25px;
    border-radius: 0 0 0 7px;
    background: rgb(0,0,0, 0.7);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    z-index:10;
    display:-ms-flexbox;
    display:flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    -webkit-box-shadow: 0 3px 6px rgba(0,0,0,0.3), 0 3px 6px rgba(0,0,0,0.3);
            box-shadow: 0 3px 6px rgba(0,0,0,0.3), 0 3px 6px rgba(0,0,0,0.3);
  }
  .movie-rating h6{
    color:white;

  }
  .star{
    color:orange;
    font-size:0.7em;
    padding:0 2px 1px 0;
  }

